import { createStore } from 'redux'; // Импортируем функцию createStore с модуля redux
import { devToolsEnhancer } from 'redux-devtools-extension'; // Импортируем функцию для разширения в браузере
import { formReducer } from './form/formReducer';

const store = createStore(formReducer, devToolsEnhancer());

export default store;
