import React, { Component } from 'react';
import { connect } from 'react-redux';
import * as actions from '../../redux/form/formActions';
import Congrats from '../Congrats/Congrats';
import Form from '../Form/Form';
import styles from './App.module.css';

class App extends Component {
  state = {
    name: '',
  };

  handleInputChange = event => {
    const { value } = event.target; // Получили значение с инпута

    this.setState({ name: value }); // Записали в стейт значение инпута
  };

  handleSubmit = event => {
    event.preventDefault();
    const { saveNameToStorage } = this.props; // Получили action из redux

    saveNameToStorage({ ...this.state }); // Передали копию состояния в метод saveNameToStorage
    this.resetState(); // Вызвали метод который сбрасывает состояние
  };

  resetState = () => {
    this.setState({
      name: '',
    });
  };

  render() {
    const { name } = this.state;
    const { nameFromStorage } = this.props;

    console.log(name);

    return (
      <div className={styles.container}>
        <Congrats nameFromStore={nameFromStorage} />
        <Form
          query={name}
          onSubmit={this.handleSubmit}
          changeInput={this.handleInputChange}
        />
      </div>
    );
  }
}

// Берет из хранилища свойства и передает в качестве props
const mapStateToProps = store => {
  return {
    nameFromStorage: store.savedName,
  };
};

const mapDispatchToProps = dispatch => ({
  saveNameToStorage: name => dispatch(actions.saveNameAction(name)),
});

export default connect(mapStateToProps, mapDispatchToProps)(App);
