import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux'; // Импортируем компонент Provider из react-redux модуля
import App from './components/App/App';
import store from './redux/store'; // Импортируем хранилище с файла хранилища

console.log(store); // Отображаем в консоле хранилище

/**
 * - Оборачиваем App в компонент Provider который получает props хранилище
 */
ReactDOM.render(
  <Provider store={store}>
    <App />
  </Provider>,
  document.getElementById('root'),
);
