import { Types } from '../actionTypes';

const defaultState = {
  savedName: '',
};

export const formReducer = (state = defaultState, action) => {
  console.log(state);
  switch (action.type) {
    case Types.SAVENAME:
      return {
        ...state,
        savedName: action.payload.name,
      };
    // Возвращаем новый обьект state, в savedName передаем значение action.payload.name

    default:
      return state;
  }
};
